import './AdminClienteDetail.css'

import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { Page } from '../../components'
import { useApiToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'

const defaultValues = {
  id: '',
  first_name: '',
  last_name: '',
  nro_documento: '',
  razon_social: '',
  email: '',
  ruc: '',
  nro_telefono: '',
  imagen_perfil: null,
}

export const AdminStaffDetail: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { adminStore, loginStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [Cliente, setCliente] = useState(adminStore.clienteDetalle || defaultValues)
  const [Origen, setOrigen] = useState('')
  const apiToast = useApiToaster()

  const setup = () => {
    if (history.location.hasOwnProperty('origen')) {
      // @ts-ignore
      setOrigen(history.location.origen)
      // @ts-ignore
      if (history.location.origen === 'header') {
        // @ts-ignore
        setCliente(loginStore)
      }
    } else {
      setOrigen('staff')
    }
  }

  React.useEffect(() => {
    setup()
  }, [])

  const editar = (row: any) => {
    adminStore.setClienteDetalle(row)
    history.push('/admin/staff/update')
  }

  const eliminar = (row: any) => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea eliminar?')) {
      return false
    }
    rootStore.environment.api.eliminarUsuarioStaff(row.id, null).then((result: any) => {
      setLoading(false)
      apiToast(result, 'Eliminado satisfactoriamente', 'eliminar')
      if (result.kind === 'ok') {
        history.push('/admin/staff')
      }
    })
  }

  const enviarCorreoRecuperacion = () => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Desea enviar un email a ' + loginStore.email + ' para cambiar su contraseña?')) {
      return false
    }
    const email = Cliente.email || ''
    rootStore.environment.api.recuperarContrasena(email).then((result: any) => {
      setLoading(false)
      apiToast(result, 'Se envió un enlace para cambiar su contraseña a su correo', 'enviar correo')
    })
  }

  if (!adminStore.clienteDetalle) {
    return <span></span>
  }
  return (
    <Page login_required admin solo_admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              {Origen && Origen === 'header' ? (
                <h3 className="text-themecolor">Mi perfil</h3>
              ) : (
                <h3 className="text-themecolor">{Cliente.email}</h3>
              )}
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                {Origen && Origen === 'staff' ? (
                  <li className="breadcrumb-item">
                    <a href="/admin/staff">Usuarios Staff</a>
                  </li>
                ) : (
                  Origen !== 'header' && (
                    <li className="breadcrumb-item">
                      <a href="/admin/fabricante/detail">Proveedor</a>
                    </li>
                  )
                )}
                {Origen && Origen === 'header' ? (
                  <li className="breadcrumb-item active">Mi perfil</li>
                ) : (
                  <li className="breadcrumb-item active">{Cliente.email}</li>
                )}
              </ol>
            </div>
          </div>
          <div className={'mb-2 text-right'}>
            {Origen && Origen == 'staff' && (
              <button
                className={'btn btn-light ml-3'}
                title={'Editar'}
                onClick={() => editar(adminStore.clienteDetalle)}
              >
                <FIcon.Edit size={14} /> Editar
              </button>
            )}
            {Origen && Origen === 'staff' && (
              <button
                className={'btn btn-light ml-3'}
                title={'Eliminar'}
                onClick={() => eliminar(adminStore.clienteDetalle)}
              >
                <FIcon.Trash size={14} /> Eliminar
              </button>
            )}
            {Origen && Origen !== 'staff' && (
              <button className={'btn btn-primary ml-3'} title={'Eliminar'} onClick={() => enviarCorreoRecuperacion()}>
                Cambiar contraseña
              </button>
            )}
          </div>
          <div className="card">
            <div className="card-body">
              <table className={'table'}>
                <tr>
                  <td>Nombre</td>
                  <td>{Cliente.first_name}</td>
                </tr>
                <tr>
                  <td>Apellido</td>
                  <td>{Cliente.last_name}</td>
                </tr>
                <tr>
                  <td>Email</td>
                  <td>
                    <a href={'mailto:' + Cliente.email}>{Cliente.email}</a>
                  </td>
                </tr>
                {Cliente.nro_documento && (
                  <tr>
                    <td>Número de documento</td>
                    <td>{Cliente.nro_documento}</td>
                  </tr>
                )}
                {Cliente.nro_telefono && (
                  <tr>
                    <td>Número de celular</td>
                    <td>
                      <a href={'tel:+' + Cliente.nro_telefono}>+{Cliente.nro_telefono}</a>
                    </td>
                  </tr>
                )}
                {Cliente.razon_social && (
                  <tr>
                    <td>Razón social</td>
                    <td>{Cliente.razon_social}</td>
                  </tr>
                )}
                {Cliente.ruc && (
                  <tr>
                    <td>RUC</td>
                    <td>{Cliente.ruc}</td>
                  </tr>
                )}
              </table>
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminStaffDetail
